import React,{useEffect} from 'react'
import './Detail.css'
import cd_1 from "../assets/images/web/cd_1.png"
import fanh from "../assets/images/web/fanh.png"
import dainpu_logo from "../assets/images/web/dainpu_logo.jpg"
import querystring from 'query-string'
import { Toast,Carousel,WingBlank  } from 'antd-mobile';
import * as types from '../store/types'
import asyncAction from '../store/asyncAction'
import {connect} from 'react-redux'



const Detail =(props)=>{
    useEffect(()=>{
        let id=props.match.params.id;
        let dataName=querystring.parse(props.location.search).dataName;
        props.getDetail(dataName,id)
    },[])

    const toastMsg=()=>{
        Toast.info("加入成功！",1)
    }

    let {des,jia,detail}=props.detail
        return (
            <div className='Detail'>
                <div className="toub_beij toub_beij_zy">
                <div className="zhongj_k zhongj_k_z">
                    <div className="jiu_feo">
                        <div className="logo_k logo_k_fanh"><a  onClick={()=>{props.history.goBack()}}><img className="fanh_ann" src={fanh} /></a></div>
                        <div className="zhongjain_daoh">
                            <ul>
                                <li className="xiangq"><a href="XiangQ_Y.html">商品</a></li>
                                <li><a href="XiangQ_Y_2.html">详情</a></li>
                                <li><a href="XiangQ_Y_3.html">评价</a></li>
                            </ul>
                        </div>
                    </div>
                    <div className="dengl">
                        <div className="caid_img" id="zhu_caid"><img src={cd_1} /></div>
                        <ul className="zhu_daoh" id="daoh_lb">
                            <em></em>
                            <li><a href="index.html">首页</a></li>
                            <li><a href="wangid_ShangP_FenL.html">商品分类</a></li>
                            <li><a href="#">扫一扫</a></li>
                            <li><a href="wanagid_GouWuChe.html">购物车</a></li>
                            <li className="wu"><a href="#">我的通城</a></li>
                        </ul>
                    </div>
                </div>
            </div>

            {detail && (
                <WingBlank>
                 <Carousel
                    autoplay
                    infinite
                >
                    <div className="swiper-slide" > <img src={`${baseUrl}${detail.auth_icon}`}/> </div>
                    <div className="swiper-slide" > <img src={`${baseUrl}${detail.auth_icon}`}/> </div>
                    <div className="swiper-slide" > <img src={`${baseUrl}${detail.auth_icon}`}/> </div>
                    <div className="swiper-slide" > <img src={`${baseUrl}${detail.auth_icon}`}/> </div>
                    <div className="swiper-slide" > <img src={`${baseUrl}${detail.auth_icon}`}/> </div>
                </Carousel>
                </WingBlank>
            )}
            <div className="xiangq_shangp_m">
                <p className="px24">{des}</p>
                <div className="xq_jiaq"><p>{jia}</p></div>
            </div>
            <div className="cans_xuanz" id="dianjisj">
                <div>活动大减价，满300立减30！！！</div>
            </div>

            <div className="xiangq_dp_k">
                <div className="dianpulogo">
                    <h1><img src={dainpu_logo}/></h1>
                    <div className="dianp_pf">
                        <p className="px24" >{detail && detail.auth}</p>
                        <div className="xx">
                            <p></p>
                            <p></p>
                            <p></p>
                            <p></p>
                            <p></p>
                            <span><em>4.00</em>分</span>
                        </div>
                    </div>
                </div>
                <div className="pf_a">
                    <p>商品描述<em>4.15</em></p>
                    <p className="z">发货速度<em>4.15</em></p>
                    <p className="you">服务质量<em>4.15</em></p>
                </div>
            </div>

            <div className="dib_daoh_b">
                <div className="gouwuc_">
                    <div className="you">
                        <div className="goum">
                            <a  onClick={()=>{
                                props.add(props.detail)
                                toastMsg()
                                setTimeout(()=>localStorage.setItem('buycar_item',JSON.stringify(props.buycar)),0)
                            }}>加入购物车</a>
                            <a className="houm" href="#">立即购买</a>
                        </div>
                    </div>
                </div>
            </div>
            </div>
        )
    }

const mapStateToProps = state =>({
    detail:state.detail,
    buycar:state.buycar
})

const mapDispatchToProps = dispatch =>({
    getDetail:(dataName,id)=>dispatch(asyncAction({
        apiname:`${dataName}/${id}`,
        typeName:types.UPDATE_DETAIL
    })),

    add:(detail)=>{
        dispatch({
            type:types.ADD_ITEM,
            payload:{...detail}
        })
    }
})

export default connect(mapStateToProps,mapDispatchToProps)(Detail)